* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/* ************************ Navbar styling ********************************** */

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 10vh;
  position: relative;
}
.navbar .list{
  text-decoration: none;
}
.bright .navbar {
  background-color: white;
  box-shadow:2px 2px 6px rgb(0, 0, 0);
}
.dark .navbar {
  background-color: rgb(0, 0, 0);
  box-shadow: 2px 2px 10px rgb(255, 255, 255);
  z-index: 3;
}
.navbar ul {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 25px;
  margin-left: 25px;
}
.navbar ul li {
  list-style: none;
  font-weight: 500;
}
.bright .navbar ul li{
  color: black;
}
.dark .navbar ul li{
  color: white;
}
.navbar img {
  margin-right: 25px;
}
.navbar .searchbar {
  display: flex;
  align-items: center;
  gap: 25px;
  height: 100%;
}
.navbar input {
  outline: none;
  border: none;
  height: 100%;
  border-radius: 8px;
  padding-right: 10px;
}
.searchbar img{
  margin-left: 5px;
  margin-right: 0;
}

.searchbar .serachinput {
  height: 50%;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center; 
}

.bright .searchbar .serachinput {
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.459);
}
.dark .searchbar .serachinput {
  box-shadow: 2px 2px 4px white;
  background-color: white;
}
.navbar button {
  font-weight: bold;
  height: 50%;
  border-radius: 8px;
  width: 96px;
  border: none;
  background-color: white;
}
.bright .navbar button{
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.514);
}
.dark .navbar button{
  box-shadow: 1px 1px 4px white;
}
.navbar ul li:hover,
button:hover,
img {
  cursor: pointer;
}

/* ************************ textbox styling ********************************** */
textarea {
  height: 50vh;
  width: 50vw;
  margin-top: 25px;
  outline: none;
  border: none;
  border-radius: 12px;
  font-size: x-large;
  padding: 20px;
}
.bright textarea{
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.473), inset -2px -2px 10px rgba(0, 0, 0, 0.11);
}
.dark textarea{
  box-shadow: 2px 2px 10px rgb(255, 255, 255), inset -2px -2px 10px rgb(255, 255, 255);
}
.mainContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 82.5vh;
}
.dark .mainContainer{
  background-color: black;
}
.mainContainer h1 {
  margin-top: -50px;
  font-size: 40px;
  font-family: "Courier New", Courier, monospace;
}
h1 span {
  color: red;
}
.bright h1{
  color: black;
}
.dark h1{
  color: white;
}
.btnContainer {
  width: 50vw;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.btn button {
  height: 35px;
  padding: 0 10px 0 10px;
  border: none;
  background-color: white;
  margin-top: 25px;
  border-radius: 8px;
  font-weight: bold;
}
.bright .mainContainer .btn button {
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.568);
}
.dark .mainContainer .btn button {
  box-shadow: 2px 2px 8px white;
}


/* ************************ About ***************************** */
.aboutContainer{
  z-index: -1;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 25px;
  width: 100vw;
  height: 82.5vh;
  font-weight: bold;
}
.aboutContainer h1{
  margin-top: 25px;
}
.dark .aboutContainer {
  background-color: black;
}
.information{
  width: 60%;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.438);
  border-radius: 12px;
  padding: 30px;
  height: 50vh;
  font-size: larger;
}

.dark .information{
  background-color: black;
  color: white;
  box-shadow: 1px 1px 8px rgb(255, 255, 255), inset -1px -1px 8px white;
}


/* **************************** Footer ********************************* */

.footer {
  height: 7.5vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(0, 0, 0);
  position: relative;
  z-index: 5;
}

.bright .footer {
  box-shadow: inset 1px 1px 36px  black;
}
.dark .footer {
  background-color: black;
  border-top: 1px solid rgba(255, 255, 255, 0.479);
  color: white;
}